import React from "react";
import { LHS, RHS, FullWidthBtn } from "./general/style/common";
import { BottomTray } from "./general/style/style";
import ProductSelectDropdown from "./ProductSelectDropdown";
import ProductThumbnail from "./ProductThumbnail";

export default function ProductCompareWithDropDown({ showDropdownsLeft, showDropdownsRight, itemSelectedLeft, itemSelectedRight, brands, product ,onCloseThumbnailLeft, onCloseThumbnailRight, modelLeft, modelRight, onBrandSelected, onModelSelected}) {
    //  console.log("ProductCompareWithDropDown props: ");
  return (
    <BottomTray className="notFixed" padding="12px 0 12px 0">
      <div className={product=="mobile"?"compare":"compare laptops"}>
        <LHS> 
          {showDropdownsLeft ? <ProductSelectDropdown brands={brands} product={product} modelSide="modelLeft" models={modelLeft} onBrandSelected={onBrandSelected} onModelSelected={onModelSelected}/> : <ProductThumbnail itemSelected={itemSelectedLeft} onClick={onCloseThumbnailLeft} />}
          </LHS>
        <RHS>
          {showDropdownsRight ? <ProductSelectDropdown brands={brands}  product={product} modelSide="modelRight" models={modelRight} onBrandSelected={onBrandSelected} onModelSelected={onModelSelected}/>: <ProductThumbnail itemSelected={itemSelectedRight} onClick={onCloseThumbnailRight} />}
        </RHS>
      </div>
    { (showDropdownsLeft ^ showDropdownsRight ) ? <FullWidthBtn className='FullWidthBtn'>Select another product to compare</FullWidthBtn> : ''}
    </BottomTray>
  );
}





